@import '../config/_breakpoints';
@import '../config/_colors';

.compare {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

.compare__header {
  height: 220px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @include media($breakpoint-md) {
    height: 200px; }
  @include media($breakpoint-sm) {
    height: 240px; } }

.compare__heading {
  font-weight: 100;
  font-size: 48px;
  text-align: center;
  margin-top: 48px;
  margin-bottom: 16px;
  opacity: 0;
  @include media($breakpoint-md) {
    font-size: 40px;
    max-width: 90vw; }
  @include media($breakpoint-sm) {
    font-size: 32px;
    max-width: 95vw; } }

.compare__input {
  font-family: Roboto;
  font-size: 48px;
  background: inherit;
  width: 240px;
  padding-left: 32px;
  font-weight: 100;
  outline: none;
  border: none;
  @include media($breakpoint-md) {
    font-size: 40px;
    width: 204px; }
  @include media($breakpoint-sm) {
    font-size: 32px;
    width: 164px;
    padding-left: 26px; } }

.compare__placeholder {
  margin-right: 4px;
  margin-left: 4px;
  position: relative;
  display: inline-block; }

.compare__placeholder::before {
  position: absolute;
  left: 4px;
  top: 1px;
  content: '$';
  pointer-events: none; }

.compare__placeholder::after {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100%;
  content: '';
  background: rgba($color-black, 0.6); }

.compare__info {
  max-width: 95vw;
  font-weight: 300;
  font-size: 23px;
  text-align: center;
  color: $color-black;
  opacity: 0;
  margin: 0;
  @include media($breakpoint-md) {
    font-size: 20px; }
  @include media($breakpoint-sm) {
    font-size: 18px; } }

.compare__boxes {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap; }
