@import '../config/_breakpoints';
@import '../config/_colors';

.compare-box {
  width: 500px;
  max-width: 90vw;
  height: 589px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: $color-black;
  color: white;
  border-radius: 10px;
  margin: 32px;
  margin-top: 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5);
  opacity: 0;
  @include media($breakpoint-lg) {
    width: 400px; }
  @include media($breakpoint-md) {
    width: 500px;
    margin: 16px;
    margin-top: 32px; } }

.compare-box__header {
  font-size: 48px;
  font-weight: 100;
  height: 130px;
  margin: 0;
  display: flex;
  align-items: center;
  text-align: center;
  @include media($breakpoint-md) {
    font-size: 36px; } }

.compare-box__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 16px;
  height: 78px;
  font-size: 28px;
  font-weight: 300;
  margin: 0;
  color: $color-white;
  @include media($breakpoint-lg) {
    font-size: 24px; }
  @include media($breakpoint-sm) {
    font-size: 18px; } }

.compare-box__prop {
  opacity: 0; }

.compare-box__value {
  opacity: 0; }

.compare-box__prop--0 {
  animation-delay: 0.2s; }
.compare-box__prop--1 {
  animation-delay: 0.4s; }
.compare-box__prop--2 {
  animation-delay: 0.6s; }
.compare-box__prop--3 {
  animation-delay: 0.8s; }
.compare-box__prop--4 {
  animation-delay: 1s; }
.compare-box__prop--5 {
  animation-delay: 1.2s; }

.compare-box__value--0 {
  animation-delay: 1.4s; }
.compare-box__value--1 {
  animation-delay: 1.6s; }
.compare-box__value--2 {
  animation-delay: 1.8s; }
.compare-box__value--3 {
  animation-delay: 2s; }
.compare-box__value--4 {
  animation-delay: 2.2s; }
.compare-box__value--5 {
  animation-delay: 2.4s; }

.compare-box__item:nth-of-type(odd) {
  background: $color-gold; }
